<template>
  <div id="search">
    <el-form :inline="true">
      <el-form-item label="書籍名">
        <el-input type="text" size="large" v-model="keyword"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onclick">検索</el-button>
      </el-form-item>
    </el-form>
    <p>※最大10件まで表示</p>
    <hr />
    <!-- マッチした書籍情報をリスト表示 -->
    <BookInfo v-for="(b, i) of books"
      :linkable="true" :book="b" :index="i + 1" :key="b.isbn"></BookInfo>
  </div>
</template>

<script>
import BookInfo from '@/components/BookInfo.vue'

export default {
  name: 'book-search',
  // ローカルコンポーネントを登録
  components: {
    BookInfo
  },
  data () {
    return {
      // 検索キーワード初期値
      keyword: 'Vue.js',
      // 検索結果
      books: []
    }
  },
  methods: {
    // [検索]ボタンで書籍情報を検索（10件表示）
    onclick: function () {
      const baseUrl = 'https://www.googleapis.com/books/v1/volumes?q=intitle:'
      const params = {
        country: 'JP'
      }
      // fetchメソッドに別名を付けている
      this.$http(baseUrl + this.keyword, { params })
        // 応答データをJSONデータとして取得
        .then((response) => {
          return response.json()
        })
        // JSON文字列の内容をbooksプロパティ（配列）に詰め替え
        .then((data) => {
          this.books = []
          for (const b of data.items) {
            const authors = b.volumeInfo.authors
            const img = b.volumeInfo.imageLinks
            this.books.push({
              id: b.id,
              title: b.volumeInfo.title,
              author: authors ? authors.join(',') : '',
              publisher: b.volumeInfo.publisher,
              published: b.volumeInfo.publishedDate,
              image: img ? img.smallThumbnail : ''
            })
          }
        })
    }
  }
}
</script>

<style scoped>
#search form {
  margin-top: 15px;
}
</style>
